*  # Columns reorder
*
*  Easily modify the column order of a table through drop-and-drag of column headers
*
*  Version: 1.0
*  Latest update: May 25, 2015
*
* ---------------------------------------------------------------------------- */
.DTCR_clonedTable {
  background-color: rgba(255, 255, 255, 0.8);
  z-index: 202;
  cursor: move;
}
.DTCR_clonedTable th,
.DTCR_clonedTable td {
  border: 1px solid #ddd !important;
}
.DTCR_pointer {
  width: 1px;
  background-color: #2196F3;
  z-index: 201;
}
/* ------------------------------------------------------------------------------
*
*  # Row Reorder extension
*
*  RowReorder adds the ability for rows in a DataTable to be reordered through
*  user interaction with the table.
*
*  Version: 1.0
*  Latest update: Nov 10, 2015
*
* ---------------------------------------------------------------------------- */
.dt-rowReorder-float {
  position: absolute!important;
  table-layout: static;
  outline: 1px dashed #777;
  outline-offset: -1px;
  background-color: #fff;
  z-index: 1030;
  cursor: move;
  opacity: 0.9;
  filter: alpha(opacity=90);
}
.dt-rowReorder-moving {
  outline: 1px solid #777;
  outline-offset: -1px;
}
.dt-rowReorder-noOverflow {
  overflow-x: hidden;
}
.dataTable td.reorder {
  text-align: center;
  cursor: move;
}
/* ------------------------------------------------------------------------------
*
*  # Fixed columns
*
*  Extension that "freezes" in place the left most columns in a scrolling DataTable
*
*  Version: 1.1
*  Latest update: Feb 25, 2016
*
* ---------------------------------------------------------------------------- */
.DTFC_Cloned {
  background-color: #fff;
  border-bottom: 0;
}
.DTFC_LeftWrapper .DTFC_Cloned.table > tbody > tr > th,
.DTFC_LeftWrapper .DTFC_Cloned.table > tbody > tr > td {
  border-right: 1px solid #ddd;
}
.DTFC_RightWrapper .DTFC_Cloned.table > tbody > tr > th,
.DTFC_RightWrapper .DTFC_Cloned.table > tbody > tr > td {
  border-left: 1px solid #ddd;
}
.DTFC_LeftBodyWrapper .DTFC_Cloned thead th:before,
.DTFC_RightBodyWrapper .DTFC_Cloned thead th:before,
.DTFC_LeftBodyWrapper .DTFC_Cloned thead th:after,
.DTFC_RightBodyWrapper .DTFC_Cloned thead th:after {
  content: none;
}
.DTFC_LeftBodyWrapper .DTFC_Cloned tbody > tr:first-child > td,
.DTFC_RightBodyWrapper .DTFC_Cloned tbody > tr:first-child > td,
.DTFC_LeftBodyWrapper .DTFC_Cloned tbody > tr:first-child > th,
.DTFC_RightBodyWrapper .DTFC_Cloned tbody > tr:first-child > th {
  border-top: 0;
}
.DTFC_Blocker {
  background-color: white;
}
@media (max-width: 768px) {
  .DTFC_LeftWrapper,
  .DTFC_RightWrapper {
    display: none;
  }
}
/* ------------------------------------------------------------------------------
*
*  # Fixed Header extension
*
*  This extension lets your users quickly determine what each column refers to
*  rather than needing to scroll back to the top of the table.
*
*  Version: 1.0
*  Latest update: Nov 9, 2015
*
* ---------------------------------------------------------------------------- */
.fixedHeader-floating,
.fixedHeader-locked {
  background-color: #fff;
  margin-top: 0;
  margin-bottom: 0;
}
.fixedHeader-floating {
  position: fixed;
}
.fixedHeader-floating + .fixedHeader-floating,
.fixedHeader-locked + .fixedHeader-floating {
  border-top: 0!important;
}
@media (max-width: 768px) {
  .fixedHeader-floating {
    display: none;
  }
}
.fixedHeader-locked {
  position: absolute;
}
@media (max-width: 768px) {
  .fixedHeader-locked {
    display: none;
  }
}
@media print {
  table.fixedHeader-floating {
    display: none;
  }
}
/* ------------------------------------------------------------------------------
