  width: 180px;
}
.table-inbox .table-inbox-message,
.table-inbox .table-inbox-name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-inbox .table-inbox-preview {
  font-weight: normal;
  color: #999999;
}
.table-inbox .table-inbox-time {
  text-align: right;
  width: 80px;
}
