  .table-inbox .table-inbox-name {
    white-space: normal;
  }
  .table-inbox .table-inbox-subject {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .table-inbox .table-inbox-star,
  .table-inbox .table-inbox-attachment,
