  height: 0; 
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,0.87);
  z-index: 2;
  cursor: pointer; 
  transition: 0.5s;
}

.overlay-content {
  margin: 0;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
  transition: display 0.2s;
}

.overlay-content p {
  font-size: 150%;
  color: rgb(73, 28, 121);
}

.icon-container {
  width: 35px;
  height:35px;
  background-color: white;
  border-radius: 50%;
  margin-top: 10px;
  box-shadow: 0px 0px 5px 1px #b3b3b3;
}

.icon-container:hover {
  top: 0.1em;
}

.icon-container i {
  font-size: 130%;
  margin-top: 7px;
  color: salmon;
}

/* otp prompt styles */

.otp-dialog {
  margin: auto 20px;
}

h4.otp-header {
  color: #5dbbc5;
  letter-spacing: 0.1px;
}

.otp-header-wrapper {
  text-align: center;
  margin-bottom: 15px;
  padding-top: 10px;
}

.otp-content {
  width: 100%;
}

.otp-content input {
  width: 50%;
  border: none;
  border-bottom: solid 1px #c7c7c7;
  text-align: center;
  font-weight: 500;
  font-size: 1.3em;
  line-height: 40px;
  letter-spacing: 1px;
}

.otp-actions {
  text-align: center;
  margin: 20px;
}

.otp-footer {
  margin: 20px;
}

.otp-info {
  color: #808080;
  font-size: 90%;
}

.otp-verify, .otp-resend {
  background-color: #5dbbc5;
  color: #fff;
  height: 30px;
  width: 90px;
  border: none;
  border-radius: 5%;
}

.otp-verify:hover:enabled, .otp-resend:hover:enabled {
  background-color: #57b1bb;
  transition: background-color 0.2s;
}

.otp-verify:disabled, .otp-resend:disabled {
  background-color: rgb(211, 206, 197);
  cursor: not-allowed;
}

.custom-z-index {
  z-index: var(--z-val);
}

.quantity-btn {
  border: 1px solid #d6d6d6;
  border-radius: 50%;
  background-color: #fff;
  box-shadow: 0 0 4px 0px #d4d4d4;
  margin: 10px 20px;
  height: 40px;
  min-width: 40px;
  transition: all 0.1s;
}

.quantity-btn:hover {
  transform: translateY(-3px);
}

.quantity-btn:active {
  transform: translateY(-1px);
}

.price_list {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-end;
}

.margin-auto {
  margin: 0 auto;
}

.w-100 {
  width: 100% !important;
}


