.card-dashboard {
    padding: 0px;
}
.card-dashboard img {
    width: 80px;
    height: 80px;
}
.list-group-dashboard {
    padding: 0px;
    background-color: #fff;
    color: #6B6B6B;
}
.list-group-item-mobile-dashboard {
    border-bottom: 2px solid #eeeded;
}
.badge-dashboard {
    width: 40px;
    height: 22px;
    padding-top: 5px;
}
.badge-info {
    background-color: #64B5F6;
    border: 1px solid #64B5F6;
}
.list-group-item-mobile-dashboard img {
    float: right;
    padding-right: 8px;
}
.thumbnail-dashboard:hover, .list-group-item-mobile-dashboard:hover, .panel-dashboard:hover {
    background-color: #d5d8d1;
}
.dash-count-primary {
    color: #64B5F6;
}
.numberCircle {
    display:inline-block;
    line-height:1px;
    border-radius:50%;
    border:2px solid #64B5F6;
    background-color: #64B5F6;
    font-size:32px;
    width: 80px;
    height: 80px;
    text-align: center;
    color: white;
}
.numberCircle span {
    display:inline-block;
    padding-top:50%;
    padding-bottom:50%;
    margin-left:8px;
    margin-right:8px;
}
.numberCircle.primary {
    border:2px solid #64B5F6;
    background-color: #64B5F6;
}
.numberCircle.danger {
    border:2px solid #ff1a1a;
    background-color: #ff1a1a;
}
.panel-dashboard {
    height: 160.83px;
}
.dashboard-text {
    color: #616A6B;
}
.dashboardTitle{
    margin-left: 135px; 
}
.dashboardImage-ht{
    margin-top: -7px;
}
.transactionCountersHeight{
    padding: 23px 17px !important;
}
.totalInvoiceHeight{
    padding: 15px;
}

@media screen and (max-width: 1024px) and (min-width: 768px) {
    .transactionVolumeHeight{
        padding: 30px 17px ;
    }
    .transactionCountersHeight{
        padding: 30px 17px !important;
    }
 
}
@media screen and (max-width: 1200px) and (min-width: 1024px) {
    .totalInvoiceHeight{
        padding: 8px;
    }
}